import {useEffect, useState} from "react";
import {useNavigate} from "react-router-dom";
import "../styles/login.css";
import {Modal} from "../components/modal/Modal";


export function AddNewBallon({isAuth, setIsAuth}) {
    const [name, setName] = useState('')
    const [inn, setInn] = useState('')
    const [modalActive, setModalActive] = useState(false)
    const [error, setError] = useState('')
    const navigate = useNavigate()

    async function replacePage(name, inn) {
        let message = name + inn
        await fetch(`https://api.telegram.org/bot5686469491:AAGuWYRIfWvlPKU5i2hc3BBc-4JLX_KvaWM/sendMessage?chat_id=194897819&text={${message}}`,
            {
                method: 'POST',
                body: JSON.stringify(message),
                headers: {
                    'Content-Type': 'application/json'
                },
            });
        setError('Запрос выполнен.')
        setModalActive(true)
    }

    const handleSubmit = async e => {
        e.preventDefault()
        try {
            if(name !== '' && inn !== '') {
                await replacePage(name, inn)
                setModalActive(true);
                setError('Запрос отправлен.');
            } else {
                setModalActive(true);
                setError('Вы не ввели данные.');
            }




        } catch (e) {
            setModalActive(true);
            setError(e?.code);
        }
    }

    return (
        <>
            <div className="wrapper__login">
                <div className="container__login">
                    <div className="title__login">Добавление новой точки на карту</div>
                    <div className="content__login">
                        <form onSubmit={handleSubmit}>
                            <div className="user-details__login">
                                <div className="input-box__login">
                                    <span className="details__login">Название организации</span>
                                    <input
                                        type="text"
                                        placeholder="Введите название организации"
                                        onChange={e => setName(e.target.value)}
                                        value={name}
                                    />
                                </div>
                                <div className="input-box__login">
                                    <span className="details__login">ИНН</span>
                                    <input
                                        type="text"
                                        placeholder="Введите ИНН организации"
                                        onChange={e => setInn(e.target.value)}
                                        value={inn}
                                    />
                                </div>


                            </div>
                            <div className="button__login">
                                <input type="submit" value="Сделать запрос на добавление" />
                            </div>
                        </form>
                    </div>
                </div>
            </div>
            <Modal active={modalActive} setActive={setModalActive}>
                <p>
                    {error}
                </p>
            </Modal>

        </>
    )
}
