import './App.css';
import "./styles/main.css";
import {useState} from "react";
import {BrowserRouter as Router, Routes, Route} from "react-router-dom";
import {Navbar} from "./components/navbar/Navbar";
import {Home} from "./pages/Home";
import {Map2} from './pages/Map'
import {Footer} from "./components/footer/Footer";
import {Register} from "./pages/Register";
import {Login} from "./pages/Login";
import {Profile} from "./pages/Profile";
import {AddNewBallon} from "./pages/AddNewBallon";

function App() {
    const [isAuth, setIsAuth] = useState(false)
    return (
    <div className="App">
        <Router>
            <Navbar isAuth={isAuth} setIsAuth={setIsAuth}/>
            <Routes>
                <Route path="/" element={<Home setIsAuth={setIsAuth} />} />
                <Route path="/map" element={<Map2 isAuth={isAuth} />} />
                <Route path="/register" element={<Register isAuth={isAuth} setIsAuth={setIsAuth} />} />
                <Route path="/login" element={<Login isAuth={isAuth} setIsAuth={setIsAuth} />} />
                <Route path="/profile" element={<Profile isAuth={isAuth} setIsAuth={setIsAuth} />} />
                <Route path="/add-new-ballon" element={<AddNewBallon isAuth={isAuth} setIsAuth={setIsAuth} />} />
            </Routes>
            <Footer />
        </Router>
    </div>
    );
}

export default App;
