import axios from "axios";

export async function updateUser(
    initials, organization, phone, email, newEmail, password, newPassword
)  {
    return await axios.post(
        'http://localhost:8000/api/update',
        {
            username: initials,
            organization: organization,
            phone: phone,
            email: email,
            newEmail: newEmail,
            password: password,
            newPassword: newPassword
        },
        {
            headers: {
                Authorization: `Bearer ${document.cookie.split('token=')[1]}`
            }
        }
    )
}

export async function getUser() {
    return await axios.get(
        'http://localhost:8000/api/getuser',
        {
            headers: {
                Authorization: `Bearer ${document.cookie.split('token=')[1]}`
            }
        }
    )
}
export async function registerUser(fullName, company, phone, email, password) {
    return await axios.post(
        'http://localhost:8000/api/register',
        {
            "fullName": fullName,
            "company": company,
            "phone": phone,
            "email": email,
            "password": password
        },
        {
            headers: {
                'content-type': 'application/json'
            }
        }
    )
}

export async function loginUser(email, password) {
    return await axios.post(
        'http://localhost:8000/api/login',
        {
            username: email,
            password: password
        },
        {
            headers: {
                'content-type': 'application/json'
            }
        }
    )
}

export async function getDataMap() {
    return await axios.get(
        'http://localhost:8000/api/files',
        {
            headers: {
                Authorization: `Bearer ${document.cookie.split('token=')[1]}`
            }
        }
    )
}


export function logoutUser() {
    function deleteAllCookies() {
        const cookies = document.cookie.split(";");

        for (let i = 0; i < cookies.length; i++) {
            const cookie = cookies[i];
            const eqPos = cookie.indexOf("=");
            const name = eqPos > -1 ? cookie.substr(0, eqPos) : cookie;
            document.cookie = name + "=;expires=Thu, 01 Jan 1970 00:00:00 GMT";
        }
    }
    deleteAllCookies()
}

export function setToken(userToken) {
    localStorage.setItem('token', JSON.stringify(userToken));
}

export function getToken() {
    return localStorage.getItem('token').replace(`"`, '').replace(`"`, '')
}

export function checkToken() {
    return !!localStorage?.getItem('token')
}
