import {Button, Map, ObjectManager, YMaps} from '@pbe/react-yandex-maps';
import '../styles/map.css'
import React, {useEffect, useState} from "react";
import data from '../yandex/BigDataYandex.json'
import {getDataMap, getUser} from "../services/auth.service";
import {useNavigate} from "react-router-dom";
import {Modal} from "../components/modal/Modal";

export function Map2({isAuth}) {
    const [modalActive, setModalActive] = useState(false)
    const [error, setError] = useState('')
    const [map, setMap] = useState(true);
    const [isConfirmed, setIsConfirmed] = useState(false)
    const navigate = useNavigate();
    useEffect(() => {

        let dataForMap = getDataMap()
        dataForMap.then((res) => {
            console.log(res)
            if(res.data === 'Неверный токен.') {
                setModalActive(true)
                setError('Неверный токен. Перенаправление.');
                setTimeout(() => navigate('/login'), 2000);
            } else {
                setPoints(JSON.parse(res.data.fileContent))
            }

            }
        ).catch((e) => {
            console.error(e)
        })

        if(isAuth) {
            const fetchdata = async () => {
                try {
                    const res = await getUser();
                    if(res.status === 200) {
                        const user = res?.data?.decoded
                        setIsConfirmed(user.isConfirmed)
                    }
                } catch (e) {
                    console.error(e)
                }
            }
            fetchdata().then(r => r)
        }


    }, [map, navigate, isAuth])

    const [points, setPoints] = useState();
    let tableData = []
    let mainTable = []


    let objects = [];




    let region = [];
    let category = [];

    let objectManager = React.createRef();


    let regForMap = [];
    let catForMap = [];

    function sortArrayByFirstTwoDigits(arr) {
        arr.sort((a, b) => {
            const firstTwoDigitsA = parseInt(a.toString().substring(0, 2));
            const firstTwoDigitsB = parseInt(b.toString().substring(0, 2));
            return firstTwoDigitsA - firstTwoDigitsB;
        });
        return arr;
    }

    const  handleButtonClick = async (button) => {
        const name = button.getAttribute('data-name');
        const coords = button.getAttribute('data-coords');
        const region = button.getAttribute('data-region');
        let message = name + ' ' + coords + ' ' + region
        let response = await fetch(`https://api.telegram.org/bot5686469491:AAGuWYRIfWvlPKU5i2hc3BBc-4JLX_KvaWM/sendMessage?chat_id=194897819&text={${message}}`,
            {
                method: 'POST',
                body: JSON.stringify(message),
                headers: {
                    'Content-Type': 'application/json'
                },
            });
        console.log(response)
    }


    data.features.forEach((item) => {
        region.push(item.properties.region)
        category.push(item.properties.direction)


        objects.push(
            {
                type: 'Feature',
                id: item.id,
                geometry: {
                    type: 'Point',
                    coordinates: item.geometry.coordinates
                },
                properties:{
                    region: item.properties.region,
                    balloonContentBody: '<div>'.toString() + '<p>'+ item.properties.balloonContentBody +'</p>'.toString() +'<button onClick={{fetch(`https://api.telegram.org/bot5686469491:AAGuWYRIfWvlPKU5i2hc3BBc-4JLX_KvaWM/sendMessage?chat_id=194897819&text={}`, { method: `POST`})}} data-name="' + item.properties.balloonContentBody.toString().replaceAll('"', '') + '" data-coords="' + item.geometry.coordinates + '" data-region="' + item.properties.region + '">Пожаловаться</button>'.toString() + '<div>'.toString()


                },
                options: {
                    iconColor: item.options.iconColor
                }
            }
        )
        tableData.push(
            <tr>
                <td>{item.properties.balloonContentBody}</td>
                <td>{item.properties.direction}</td>
                <td>{item.properties.region}</td>
            </tr>)
    })

    if(!isAuth || !isConfirmed) {
        tableData = tableData.slice(0, 5)
        tableData.push(<tr>
            <td>У вас нет подтверждения</td>
            <td>Подайте заявку в личном кабинете</td>
            <td onClick={() => navigate('/profile')}><strong>ЛК</strong></td>
        </tr>)
    }



    let reg = new Set(region)
    let cat = new Set(category)

    reg = Array.from(reg)
    cat = Array.from(cat)

    let cnt1 = 0;
    let numbers_of_cat = []
    let help = cat;

    let cat_info = help.map((id) =>  {
        cnt1 = 0;
        category.forEach((item) => {
            if(id === item) {
                cnt1 += 1;
            }
        }
        )
        numbers_of_cat.push([id, cnt1])
    } )

    numbers_of_cat = numbers_of_cat.map(
        (item) =>  <div className="cat_prop" key={crypto.randomUUID()} id={item[0]}>
            <p className="cat_prop_p">{item[0]}</p>
            <p className="cat_prop_p_number"><strong>{item[1]}</strong></p>
        </div>
    )


    console.log(numbers_of_cat)


    reg = sortArrayByFirstTwoDigits(reg)
    cat = sortArrayByFirstTwoDigits(cat)





    function reRender (regForMap, catForMap, typ, prop) {
        console.log(prop)
        if(typ === 1) {
            if(regForMap.includes(prop)){
                for(let i = 0; i < regForMap.length; i++) {
                    if(prop === regForMap[i]) {
                        regForMap.splice(i, 1)
                        document.getElementById(prop).style.color = 'rgb(0, 0, 0)';
                    }
                }
            } else  {
                regForMap.push(prop)
                document.getElementById(prop).style.color = 'rgb(0, 163, 92)';
            }
        }
        if (typ === 2){
            if(catForMap.includes(prop)){
                for(let j = 0; j < catForMap.length; j++) {
                    if(prop === catForMap[j]) {
                        catForMap.splice(j, 1)
                        document.getElementById("dynamic-" + prop).style.color = 'rgb(0, 0, 0)';
                    }
                }
            } else {
                catForMap.push(prop)
                document.getElementById("dynamic-" + prop).style.color = 'rgb(0, 163, 92)';
            }
        }

        if(regForMap.length === 0 && catForMap.length === 0) {
            objectManager?.current?.setFilter()
        } else {
            objectManager?.current?.setFilter(function(object) {
                return (regForMap.includes(object.properties.region) || catForMap.includes(object.properties.direction))
            })

        }
    }

console.log(cat)

    reg = reg.map(
        (reg) => <div key={crypto.randomUUID()} id={reg} onClick={() => reRender(regForMap, catForMap, 1, reg)}>{reg}</div>
    )


    cat = cat.map(
        (cat) => <div key={crypto.randomUUID()} id={'dynamic-'+cat} onClick={() => reRender(regForMap, catForMap, 2, cat)}>{cat}</div>
    )




    const defaultState = {
        center: [55.76, 37.64],
        zoom: 10,
    };
    const myStyle = {
        width: "calc(100vw)",
        height: "calc(100vh - 177px)"
    }

    return(
        <>
            <div className="container_for_features">
                <div className="container_for_map">
                    {map && <YMaps>
                        <Map
                            defaultState={defaultState}
                            style={myStyle}
                        >
                            <ObjectManager
                                instanceRef={objectManager}
                                options={{
                                    clusterize: true,
                                    gridSize: 100,
                                    clusterDisableClickZoom: true
                                }}
                                objects={{
                                    openBalloonOnClick: true,
                                }}
                                defaultFeatures={objects}
                                modules={[
                                    "objectManager.addon.objectsBalloon",
                                    "objectManager.addon.objectsHint",
                                    "objectManager.OverlayCollection"
                                ]}
                            />


                        </Map>
                    </YMaps>}
                    {!map &&
                        <div className="scroll-table">
                            <table>
                                <thead>
                                <tr>
                                    <th>Название</th>
                                    <th>Направление</th>
                                    <th>Регион</th>
                                </tr>
                                </thead>
                            </table>
                            <div className="scroll-table-body">
                                <table>
                                    <tbody>
                                    {tableData}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    }
                </div>
                <div className="container_for_map_info">
                    {numbers_of_cat}
                </div>
                <div className="container_for_filter">
                    <nav role='navigation'>
                        <div id="menuToggle">
                            <input type="checkbox" />
                            <span></span>
                            <span></span>
                            <span></span>
                            <ul id="menu">
                                <li>
                                    <p>Категории</p>

                                    <div className="region">
                                        {cat}
                                    </div>

                                </li>
                                <li>
                                    <p>Регион и субъект</p>

                                    <div className="region">
                                        {reg}
                                    </div>

                                </li>
                                <li>
                                    <p>Смените вид</p>

                                    <div className="region" onClick={() => setMap(!map)}>
                                        {map ? 'Таблица' : 'Карта'}
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </nav>
                </div>
            </div>
            <Modal active={modalActive} setActive={setModalActive}>
                <p>
                    {error}
                </p>
            </Modal>
        </>
    )
}
